import { Component, h, State } from '@stencil/core';
import { Sender } from './enums/sender.enum';
import ChatMessage from './interfaces/ChatMessage.interface';
import state from '../../store/store'


@Component({
  tag: 'chat-component',
  styleUrl: 'chat-component.scss',
  shadow: true,
})
export class ChatComponent {
  @State() messages: ChatMessage[] = [{ text: "¡Hola! Soy Vivi, la asistente virtual de Davivienda.", sender: Sender.VIVI }, { text: "Si tiene clave virtual, ingrese con ella para recibir atención personalizada.", sender: Sender.VIVI }, { text: "¿En qué le puedo ayudar?", sender: Sender.VIVI }];

  componentWillLoad() {

    state.messages = this.messages;

  }
  addMargin(index: number) {

    if (index === 0) {
      return 'chat-component__initial-margin'
    }
    else {
      if (state.messages[index].sender !== state.messages[index - 1].sender) {
        return 'chat-component__initial-margin'
      }
    }

  }


  render() {
    return (
      <div class={'chat'}>
        {
          state.messages.map((message, i) =>
            <div key={i} class={this.addMargin(i)}>
              <chat-bubble text={message.text} sender={message.sender} ></chat-bubble>
            </div>
          )
        }
      </div>
    );
  }

}
